.App {
  position: relative;
}

html {
  -ms-overflow-style: none;
}

html::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.hw {
  display: flex;
  flex-direction: column;
  gap: 50px;
  padding: 40px;
}

.hwTitle {
  padding: 40px 40px 10px;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  color: #000000;
}

.mainWrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.firstTitle {
  padding-top: 140px;
}
