.item {
  padding-left: 20px;
  height: 48px;

}
.item td {
  border-bottom: 1px solid #E5E5E5;;
}

.item:last-of-type td {
  border: none;
}

.users {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  color: #000000;
}

.buttonsContainer {
  align-self: flex-end;
}

.container {
  width: 610px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.thead {
  height: 36px;
  background: #E5E5E5;
  font-size: 15px;
  font-weight: 900;
}

.nameCol {
  width: 50%;
  padding-left: 20px;
}

.ageCol {
  width: 50%;
}

.dataAge {
  font-weight: 500;
}