.buttonContainer {
    display: flex;
    gap: 24px;
    margin-bottom: 32px;
}

.all {
    --color: #153ba4;
}

.low {
    --color: #d2c217;
}

.high {
    --color: #4cc7a4;
}

.middle {
    --color: #cc00c2;
}

.button {
    padding: 5px 15px;

    background: transparent;
    color: var(--color);

    border: 2px solid var(--color);
    border-radius: 15px;
    outline: none;

    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
}

.button:active {
    background: var(--color);
    color: white;
}

.active {
    background: var(--color);
    color: white;
}

.affairs {
    display: flex;
    gap: 17px;
    flex-wrap: wrap;
    max-width: 450px;
}

.wrapper {
    width: 470px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    border-radius: 20px;
    border: 10px groove rgba(218, 22, 22, 0.9);
}
